import React from "react";
import { Link } from "react-router-dom";
import "./Modal.css";

function Modal(props) {
  return (
    <div
      className={
        props.modal
          ? "modal_container modal_popup"
          : "modal_container modal_popdown"
      }
    >
      <div className="close_icon">
        <i class="fas fa-times " onClick={() => props.setmodal(false)}></i>
      </div>

      <h1>Get Free Access to 50+ Courses</h1>
      <p>
        Sign up for the Unplugged On-Demand Courses for free. Connect With the
        community with recorded learning videos,demos,and more!
      </p>
      <Link to="/register" className="register_button">
        Get Register
      </Link>
    </div>
  );
}

export default Modal;
